<template>
  <div>
    <Row :gutter="10">
      <Col span="9" style="margin-left: 5px;padding-top: 40px;">
      <Checkbox v-model="data.customerName"></Checkbox>
      <Checkbox v-model="data.serviceItems"></Checkbox>
      <Checkbox v-model="data.cashPerformance"></Checkbox>
      <Checkbox v-model="data.highEndProjects"></Checkbox>
      <Checkbox v-model="data.adviser"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.facialPracticeAppoint"></Checkbox>
      <Checkbox v-model="data.facialPracticeNonSpecifiedt"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.physicalExerciseAppoint"></Checkbox>
      <Checkbox v-model="data.physicalExerciseNonSpecifiedt"></Checkbox>
      </Col>
      <Col span="4">
      <h3></h3>
      <Checkbox v-model="data.productCash"></Checkbox>
      <Checkbox v-model="data.superDiscount"></Checkbox>
      <Checkbox v-model="data.productSnap"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.claspCourseName"></Checkbox>
      <Checkbox v-model="data.claspCourseMoney"></Checkbox>
      </Col>
      <Col span="1">
      <Button class="hy_btn btn" @click="saveData()"></Button>
      </Col>

    </Row>
  </div>
</template>

<script>
  import { findShopDailyDetails ,saveOrUpateShopDailyDetails} from '../../interface';
  export default {
    name: "t7",
    data () {
      return {
        data: {
          adviser: false,
          cashPerformance: false,
          claspCourse: false,
          claspCourseMoney: false,
          claspCourseName: false,
          customerName: false,
          employeeName: false,
          facialPractice: false,
          facialPracticeAppoint: false,
          facialPracticeNonSpecifiedt: false,
          highEndProjects: false,
          physicalExercise: false,
          physicalExerciseAppoint: false,
          physicalExerciseNonSpecifiedt: false,
          product: false,
          productCash: false,
          productSnap: false,
          serviceItems: false,
          storeId: '',
          superDiscount: false
        }
      }
    },
    created() {
      this.getData();
    },
    methods: {
      check(val){
        if(val == null){
          val = false;
          return val;
        }else {
          return val;
        }
      },
      getData() {
        this.$ajax({
          method: 'get',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          url: findShopDailyDetails()+"?id="+this.$route.params.id,
        }).then((res) => {
          for( var item in res.data){
            res.data[item] = this.check(res.data[item]);
          }
          this.data= res.data;
        }).catch((error) => {
          this.$Message.error('');
        });
      },
      saveData(){
        this.$ajax({
          method: 'post',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          url: saveOrUpateShopDailyDetails(),
          data: this.data,
        }).then((res) => {
          this.$Message.success('');
        }).catch( (error) =>{
          this.$Message.error('');
        })

      }
    }
  }
</script>

<style scoped>
  h3{
    margin: 10px 0;
  }
  .btn{
    margin-top: 25px;
  }
</style>



