<template>
  <div>
    <Row :gutter="24" class="option" v-if="isSystem == 'true'">
      <Col span="4">
        <Input v-model="serch" placeholder="" style="margin-top: -1px">
        <span slot="append" class="serc" @click="serc"></span>
        </Input>
      </Col>
      <Col span="2" v-if="searchActive">
      <Button class="hy_btn" @click="Return"></Button>
      </Col>
      <Col span="2">
        <Button class="hy_btn"  @click="newEm"></Button>
      </Col>
    </Row>
    <Table :row-class-name="rowClassName" :columns="columns1" :data="data1"></Table>
    <div class="page" v-if="isSystem == 'true'">
      <Page :total="pages*10" @on-change="getPage" style="float: right;"></Page>
    </div>


    <Modal  v-model="storeFlag" :title="store" :mask-closable="false" @on-ok="newStore">
      <Input v-model="storeVal.staffName" placeholder="" style="width: 310px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.phoneNumber" @on-keyup="storeVal.phoneNumber=check1(storeVal.phoneNumber)" placeholder="" style="width: 290px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.storeName" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="storeVal.provinceId" style="width:150px" :transfer=true  placeholder="" @on-change="getCities(storeVal.provinceId)">
        <Option :value="item.id" :key="item.id" v-for="item in provincesData">{{item.name}}</Option>
      </Select>
      <Select v-model="storeVal.cityId" style="width:150px" :transfer=true  placeholder="">
        <Option :value="item.id" :key="item.id" v-for="item in citiesData">{{item.name}}</Option>
      </Select>
      <br/>
      <br/>
      <Input v-model="storeVal.address" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.telephone" @on-keyup="storeVal.telephone=check1(storeVal.telephone)"  placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.managementCycle" @on-keyup="storeVal.managementCycle=check1(storeVal.managementCycle)" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="storeVal.storeType" style="width:300px" :transfer=true  placeholder="">
        <Option value="1"></Option>
        <Option value="2"></Option>
      </Select>
      <br/>
      <br/>
      <Select v-model="storeVal.operationMode" style="width:300px" :transfer=true  placeholder="">
        <Option value="1"></Option>
        <Option value="2"></Option>
      </Select>
      <br/>
      <br/>
      <RadioGroup v-model="storeVal.storeStatus" type="button" v-show="store==''">
        <Radio label=""></Radio>
        <Radio label=""></Radio>
        <Radio label=""></Radio>
      </RadioGroup>
    </Modal>

    <Modal  v-model="questionFlag" :mask-closable="false" title="" @on-ok="ok" width="80%">
      <Input v-model="question.staffName" placeholder="" style="width: 300px"/>
      &nbsp;
      <Input v-model="question.phoneNumber" @on-keyup="question.phoneNumber=check1(question.phoneNumber)" placeholder="" style="width: 275px"/>
      <br/>
      <br/>
      : <Input v-model="question.provinceName" placeholder="" style="width: 316px"/>
      &nbsp;
      <Input v-model="question.shopSquare" @on-keyup="question.shopSquare=check1(question.shopSquare)" placeholder="" style="width: 288px"/>
      <br/>
      <br/>
      <Input v-model="question.annualRent" @on-keyup="question.annualRent=check2(question.annualRent)"  placeholder="" style="width: 300px"/>
      &nbsp;
      <Input v-model="question.severalStores" @on-keyup="question.severalStores=check1(question.severalStores)" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="question.numberOfRooms" @on-keyup="question.numberOfRooms=check1(question.numberOfRooms)" placeholder="" style="width:288px"/>
      &nbsp;
      <Input v-model="question.numberOfBeds" @on-keyup="question.numberOfBeds=check1(question.numberOfBeds)" placeholder="" style="width: 288px"/>
      <br/>
      <br/>
      <Input v-model="question.numberOfEmployees" @on-keyup="question.numberOfEmployees=check1(question.numberOfEmployees)"  placeholder="" style="width:288px"/>
      &nbsp;
      <Input v-model="question.areThereAnySales" placeholder="" style="width: 264px"/>
      <br/>
      <br/>
      <Input v-model="question.areThereAnyShop" placeholder="" style="width: 288px"/>
      &nbsp;
      <Input v-model="question.cashFlow" @on-keyup="question.cashFlow=check2(question.cashFlow)" placeholder="" style="width: 192px"/>
      <br/>
      <br/>
      <Input v-model="question.practiceInTheShop" @on-keyup="question.practiceInTheShop=check2(question.practiceInTheShop)"  placeholder="" style="width: 216px"/>
      &nbsp;
      <Input v-model="question.innPassengerFlow" @on-keyup="question.innPassengerFlow=check2(question.innPassengerFlow)" placeholder="" style="width: 216px"/>
      <br/>
      <br/>
      <Input v-model="question.productPerformance" @on-keyup="question.productPerformance=check2(question.productPerformance)"  placeholder="" style="width: 192px"/>
      &nbsp;
      <Input v-model="question.directProductBrand" placeholder="" style="width: 264px"/>
      <br/>
      <br/>
      <Input v-model="question.brandNumber" @on-keyup="question.brandNumber=check1(question.brandNumber)" placeholder="" style="width: 240px"/>
      &nbsp;
      <Input v-model="question.typeOfInstrument" placeholder="" style="width: 288px"/>
      <br/>
      <br/>
      <Input v-model="question.numberOfInstruments" @on-keyup="question.numberOfInstruments=check1(question.numberOfInstruments)" placeholder="" style="width: 288px"/>
      &nbsp;
      <Input v-model="question.instrumentBrand" placeholder="" style="width: 288px"/>
      <br/>
      <br/>
      <Input v-model="question.salarySystem" placeholder="" style="width: 288px"/>
      &nbsp;
      12<Input v-model="question.employeeSalary" @on-keyup="question.employeeSalary=check2(question.employeeSalary)" placeholder="12" style="width: 250px"/>
      <br/>
      <br/>
      <Input v-model="question.projectPriceList" placeholder="" style="width: 276px"/>
      &nbsp;
      <Input v-model="question.card" placeholder="" style="width: 312px"/>
      <br/>
      <br/>
      <Input v-model="question.extensionCard" placeholder="" style="width: 300px"/>
      &nbsp;
      <Input v-model="question.oneCard1" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="question.extensionMethod" placeholder="" style="width: 264px"/>
      &nbsp;
      <Input v-model="question.newCustomers" @on-keyup="question.newCustomers=check1(question.newCustomers)" placeholder="" style="width: 216px"/>
      <br/>
      <br/>
      <Input v-model="question.numberOfTransactions" @on-keyup="question.numberOfTransactions=check1(question.numberOfTransactions)" placeholder="" style="width: 288px"/>
      &nbsp;
      <Input v-model="question.numberOfTransactions1" placeholder="" style="width: 276px"/>
      <br/>
      <br/>
      <Input v-model="question.numberOfTransactions2" placeholder="" style="width: 276px"/>
      &nbsp;
      <Input v-model="question.numberOfCustomers" @on-keyup="question.numberOfCustomers=check1(question.numberOfCustomers)" placeholder="" style="width: 265px"/>
      <br/>
      <br/>
      <Input v-model="question.theNumberOfRenewals" @on-keyup="question.theNumberOfRenewals=check1(question.theNumberOfRenewals)" placeholder="" style="width: 252px"/>
      <br/>
      <br/>
      <Input v-model="question.theBiggestProblem" type="textarea" :autosize="true" placeholder="" style="width: 265px"/>
      <br/>
      <br/>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  import { findStoreList,findStoreListById, newStore, editStore,deleteStore,getProvinces,getCities,checkStorePhone } from '../../interface';
  export default {
    name: 's_index',
    data () {
      return {
        mdstaus: 1,
        storeFlag: false,
        questionFlag: false,
        store: '',
        serch: '',
        searchActive: false,
        storeVal: {
          id: '',
          storeName: '',
          cityName:'',
          telephone: '',
          staffName: '',
          phoneNumber: '',
          operationMode: '',
          storeType: '',
          managementCycle: '',
          address: '',
          storeStatus: '',
          provinceId:'',
          cityId:'',
        },
        question: {
          id: '',
          staffName: '',
          phoneNumber: '',
          provinceName: '',
          shopSquare: '',
          annualRent: '',
          severalStores: '',
          numberOfRooms: '',
          numberOfBeds: '',
          numberOfEmployees: '',
          areThereAnySales: '',
          areThereAnyShop: '',
          cashFlow: '',
          practiceInTheShop: '',
          innPassengerFlow: '',
          productPerformance: '',
          directProductBrand: '',
          brandNumber: '',
          typeOfInstrument: '',
          numberOfInstruments: '',
          instrumentBrand: '',
          salarySystem: '',
          employeeSalary: '',
          projectPriceList: '',
          card: '',
          extensionCard: '',
          oneCard1: '',
          extensionMethod: '',
          newCustomers: '',
          numberOfTransactions: '',
          numberOfTransactions1: '',
          numberOfTransactions2: '',
          numberOfCustomers: '',
          theNumberOfRenewals: '',
          theBiggestProblem: '',
        },
        columns1: [
          {
            title: '',
            key: 'code'
          },
          {
            title: '',
            key: 'storeName'
          },
          {
            title: '',
            key: 'address'
          },
          {
            title: '',
            key: 'phoneNumber'
          },
          {
            title: '',
            key: 'managementCycle'
          },
          {
            title: '',
            key: 'storeStatus',
          },
          {
            title: '',
            width: 200,
            key: 'action',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.check(params.row)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.change(params.row)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small',
                    disabled: params.row.status==3?true:false,
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      if(params.row.status ==3 ){
                        this.$Message.error('');
                      }
                      this.manerge(params.row.id);
                    }
                  }
                }, ''),
              /*  h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small',
                    disabled: params.row.status==3?true:false,
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      if(params.row.status ==3 ){
                        this.$Message.error('');
                      }
                      this.Delete(params.row.id);
                    }
                  }
                }, ''),*/
              ]);
            }
          }
        ],
        data1: [],
        total:'',
        page:1,
        pagesize: 10,
        pages:'',
        provincesData:[],
        citiesData:[],
        successTag:'',
        tag:1,
        isSystem: sessionStorage.getItem('isSystem'),
        storeId: sessionStorage.getItem('storeId'),
      }
    },
    created() {
      this.getData();
      this.getProvinces();
    },
    methods: {
      newStore() {
        if(this.storeVal.staffName ==''|| this.storeVal.phoneNumber ==''|| this.storeVal.storeName ==''|| this.storeVal.address ==''|| this.storeVal.telephone ==''|| this.storeVal.managementCycle ==''|| this.storeVal.storeType ==''|| this.storeVal.operationMode ==''|| this.storeVal.provinceId=='' || this.storeVal.cityId==''){
          this.$Message.warning('');
          return;
        }
        let URL;
        if(this.store == '') {
          URL = editStore();
        }else{
          URL = newStore();
          this.$ajax({
            method:'GET',
            url:checkStorePhone()+'?phoneNumber='+this.storeVal.phoneNumber
          }).then( (res)=>{
            this.successTag = res.success;
          }).catch( (err)=>{
          })
          if(this.successTag == false){
            this.$Message.error('');
            return;
          }
        }


        this.$ajax({
          method: 'POST',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          data: this.storeVal,
          url: URL,
        }).then((res) => {
          this.$Message.success('');
          this.getData();
        }).catch((error) => {
        });
      },
      getData(){
        this.tag = 1;
        if(this.isSystem == 'true'){
          this.getList('',this.page,this.pagesize);
        }else{
          this.getList(this.storeId,this.page,this.pagesize);
        }
      },
      getList(name,page,pagesize) {
        if(name==''||name==null){
          var URL = findStoreList()+'?page='+page+'&pageSize='+pagesize;
        }else{
          URL = findStoreList()+'?id='+name+'&page='+page+'&pageSize='+pagesize;
        }
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url:URL,
        }).then((res) => {
          this.data1 = res.data.results;
          this.pages = res.data.pages;
        }).catch((error) => {
        });
      },
      getSearch(name,page,pagesize) {
        this.tag = 2;
        if(name==''||name==null){
          var URL = findStoreList()+'?page='+page+'&pageSize='+pagesize;
        }else{
          URL = findStoreList()+'?name='+name+'&page='+page+'&pageSize='+pagesize;
        }
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url:URL,
        }).then((res) => {
          this.data1 = res.data.results;
          this.pages = res.data.pages;
        }).catch((error) => {
        });
      },
      getPage(current){
        this.page = current;
        if(this.tag == 1){
          this.getData();
        }else if(this.tag == 2){
          this.getSearch(this.serch,this.page,this.pagesize);
        }

      },
      getProvinces(){
        this.$ajax({
          method: 'GET',
          url: getProvinces()
        }).then( (res) =>{
          this.provincesData = res.data;
        })
      },
      getCities(id){
        this.$ajax({
          method: 'GET',
          url: getCities()+id,
        }).then( (res) =>{
          this.citiesData = res.data;
        })
      },
      rowClassName (row, index) {
        if (row.storeStatus == '') {
          return 'jyinfo';
        } else if (row.storeStatus == '') {
          return 'jydanger';
        } else if (row.storeStatus == '') {
          return 'jydis';
        }
        return '';
      },
      serc() {
        if(this.serch == '') {
          this.$Message.error('');
        }else{
          this.searchActive = true;
          this.getSearch(this.serch,this.page,this.pagesize);
        }
      },
      Return(){
        this.searchActive = false;
        this.serch = '';
        this.getData();
      },
      newEm() {
        this.storeFlag = true;
        this.store = '';
        this.clearNew();
      },
      ok() {
        if(this.storeVal.staffName ==''|| this.storeVal.telephone ==''|| this.storeVal.storeName ==''|| this.storeVal.address ==''|| this.storeVal.phoneNumber ==''|| this.storeVal.managementCycle ==''|| this.storeVal.storeType ==''|| this.storeVal.operationMode ==''|| this.storeVal.provinceId=='' || this.storeVal.cityId==''){
          this.$Message.warning('');
          return;
        }
        this.$ajax({
          method: 'POST',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          data: this.question,
          url: editStore(),
        }).then((res) => {
          this.$Message.success('');
          this.searchActive = false;
          this.serch = '';
          this.getData();
        }).catch((error) => {
        });
      },
      change(data) {
        this.storeFlag = true;
        this.store = '';
        this.storeVal = JSON.parse(JSON.stringify(data));
      },
      manerge(id) {
        this.$router.push({path:'s_datile/'+id+'/c_index'});
      },
      Delete(id){
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },

          url: deleteStore()+'?id='+id,
        }).then((res) => {
          this.$Message.success('');
          this.searchActive = false;
          this.serch = '';
          this.getData();
        }).catch((error) => {
        });
      },
      check(data) {
        this.questionFlag = true;
        this.question = JSON.parse(JSON.stringify(data));
      },
      clearNew() {
        this.storeVal = {
          id: '',
            storeName: '',
            telephone: '',
            staffName: '',
            phoneNumber: '',
            operationMode: '',
            storeType: '',
            managementCycle: '',
            address: '',
            storeStatus: '',
        };
      },
      check1(value){
        return value.replace(/[^\d]/g,'');
      },
      check2(value){
        return value.replace(/[^\d\.]/g,'');
      },

    },
  };
</script>

<style scoped>
  .serc{
    cursor: pointer;
  }
  .page{
    height: 50px;
    margin-top:20px;
    padding-right: 20px;

    background: #fff;
    /*border: 1px solid #000;*/

  }
</style>



