<template>
  <div>
    <h2 style="padding: .6rem;"></h2>
    <Row :gutter="24" class="option">
      <Col span="2">
        <Button class="hy_btn" @click="newEm"></Button>
      </Col>
    </Row>
    <Table :columns="columns" :data="data"></Table>

    <Modal  v-model="storeFlag" :mask-closable="false" :title="store" @on-ok="optai">
      <div class="com">
        <Input v-model="job.postName" placeholder="" style="width: 300px"/>
      </div>
      <div class="com">
        <Input v-model="job.postDuties" placeholder="" style="width: 300px"/>
      </div>
      <div class="com">
        <Input v-model="job.workflow" placeholder="    " style="width: 300px"/>
      </div>
      <!--<div class="com">-->
        <!--<Input v-model="job.achievements" placeholder="" style="width: 264px"/>-->
      <!--</div>-->
      <!--<div class="com">-->
        <!--<Input v-model="job.technicalExamination" placeholder="" style="width: 300px"/>-->
      <!--</div>-->
      <div class="com">
        <Select v-model="job.employeeType"  placeholder="" style="width:300px" :transfer=true>
        <Option value="1" ></Option>
        <Option value="2" ></Option>
        <Option value="3" ></Option>
      </Select>
      </div>
      <div class="com">
        <Select v-model="job.employeeRank"  placeholder="" style="width:300px" :transfer=true>
        <Option value="1" ></Option>
        <Option value="2" ></Option>
      </Select>
      </div>
      <div class="com">
        <Select v-model="job.checkWorkAttendance" :transfer=true style="width:300px">
          <Option value="" ></Option>
          <Option value="" ></Option>
        </Select>
      </div>
      <div class="com">
        <Select v-model="job.dataRankings" :transfer=true style="width:274px">
          <Option value="" ></Option>
          <Option value="" ></Option>
          <Option value="" ></Option>
          <Option value="" ></Option>
        </Select>
      </div>
      <div class="com" v-if="job.dataRankings == ''">
        <Input v-model="job.multiMonthCash" placeholder="" style="width: 300px"  @on-keyup="job.multiMonthCash=check2(job.multiMonthCash)" ng-pattern="/[^a-zA-Z]/"/>
      </div>
      <div class="com">
        <Input v-model="job.shareAmount" placeholder="" style="width: 300px" @on-keyup="job.shareAmount=check2(job.shareAmount)" ng-pattern="/[^a-zA-Z]/"/>
      </div>
      <div class="com">
        <Input v-model="job.dividendManagement" placeholder="" style="width: 300px"/>
      </div>
      <div class="com">
        
        <Select v-model="job.natureOfEquity" :transfer=true style="width:297px">
          <Option value="" ></Option>
          <Option value="" ></Option>
        </Select>
      </div>
      <br/>
      <br/>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  import {findPostList, findPostsave, findPostedit,deletePost} from '../../interface';

  export default {
    name: 'e_index',
    created() {
      this.getList(1);
    },
    data(){
      return {
        storeFlag: false,
        store: '',
        job: {
          id : '',
          storeId: '',
          postName: '',
          postDuties: '',
          workflow: '',
          achievements: '',
          technicalExamination: '',
          checkWorkAttendance: '',
          multiMonthCash: '',
          classes: '',
          shareAmount: '',
          dividendManagement: '',
          natureOfEquity: '',
          dataRankings: '',
          employeeType:'',
          employeeRank:''

        },
        columns: [
          {
            title: '',
            key: 'id',
          },
          {
            title: '',
            key: 'postName',
          },
          {
            title: '',
            key: 'postDuties',
          },
          {
            title: '',
            key: 'action',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.mannger(params.row)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.Delete(params.row)
                    }
                  }
                }, ''),
              ]);
            }
          }
        ],
        data: [],
      };
    },
    methods: {
      getList(page) {
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url: findPostList()+"?id="+this.$route.params.id+"&page="+page+"&pageSize=30",
        }).then((res) => {
          this.data = res.data.results;
        }).catch((error) => {
        });
      },
      optai() {
        /*if(this.job.postName == '' || this.job.postDuties == '' || this.job.workflow == '' || this.job.achievements == '' || this.job.technicalExamination == ''){
          this.$Message.warning('');
          return false;
        }*/
        this.job.storeId = this.$route.params.id;
        let URL = findPostsave();
        if( this.store == '') {
          URL = findPostedit();
        };
        this.$ajax({
          method: 'POST',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          data: this.job,
          url: URL,
        }).then((res) => {
          this.$Message.success('');
          this.getList(1);
        }).catch((error) => {
          this.$Message.error('');
        });
      },
      newEm() {
        this.storeFlag = true;
        this.store = '';
        this.job = {
          id : '',
            postName: '',
            postDuties: '',
            workflow: '',
            achievements: '',
            technicalExamination: '',
            checkWorkAttendance: '',
          employeeType:'',
          employeeRank:''
        };
      },
      mannger(data) {
        this.storeFlag = true;
        this.store = '';
        this.job = JSON.parse(JSON.stringify(data));
      },
      Delete(data){
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url: deletePost()+'?id='+data.id,
        }).then((res) => {
          this.$Message.success('');
          this.getList(1);
        }).catch((error) => {
          this.$Message.error('');
        })
      },
      check(value){
        return value.replace(/[^\d]/g,'');
      },
      check2(value){
        return value.replace(/[^\d\.]/g,'');
      }
    }
  };
</script>

<style scoped>
  .com{
    margin: 10px 0;
  }
</style>



