<template>
  <div class="registerResult">

    <div class="layout-logo-left"></div>
    <div style="width:450px;margin: 0px auto 20px; padding-left: 30px;">
     <Row>
       <Col span="6" style="line-height: 30px;">:</Col>
       <Col span="12"> <Input v-model="search" placeholder="" style="width: 200px;">
       <span slot="append" class="searc" @click="searc"></span>
       </Input></Col>
       <Col span="2">       <Button class="hy_return" size="large"  @click="returnLogin"></Button>
       </Col>
     </Row>

    </div>
    <div v-show="isShow">
      <Input v-model="storeVal.staffName" placeholder="" style="width: 315px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.phoneNumber" @on-keyup="storeVal.phoneNumber=check1(storeVal.phoneNumber)" disabled placeholder="" style="width: 290px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.storeName" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="storeVal.provinceId" style="width:150px" :transfer=true  placeholder="" @on-change="getCities(storeVal.provinceId)">
      <Option :value="item.id" :key="item.id" v-for="item in provincesData">{{item.name}}</Option>
    </Select>
      <Select v-model="storeVal.cityId" style="width:150px" :transfer=true  placeholder="">
        <Option :value="item.id" :key="item.id" v-for="item in citiesData">{{item.name}}</Option>
      </Select>
      <br/>
      <br/>
      <Input v-model="storeVal.address" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.telephone" @on-keyup="storeVal.telephone=check1(storeVal.telephone)"  placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.managementCycle" @on-keyup="storeVal.managementCycle=check1(storeVal.managementCycle)" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="storeVal.storeType" style="width:300px" placeholder="">
      <Option value="1"></Option>
      <Option value="2"></Option>
    </Select>
      <br/>
      <br/>
      <Select v-model="storeVal.operationMode" style="width:300px" placeholder="">
      <Option value="1"></Option>
      <Option value="2"></Option>
    </Select>
      <br/>
      <br/>
      <div  style="width: 400px;margin:0 auto 50px;padding-left: 18px;text-align: left;">
        <span v-if="storeVal.auditStatus==1" style="color: orange;">{{status}}</span>
        <span v-if="storeVal.auditStatus==3" style="color: red;">{{status}}{{storeVal.causeOfFailure}}</span>
<!--v-if="storeVal.auditStatus == '3'"-->
      </div>
      <Button class="hy_btn"  style="margin-left:20px;" size="large" @click="ok"></Button>
    </div>
    <div v-show="isShow2" style="width: 400px;margin:0 auto 50px;padding-left: 18px;text-align: left;">
      {{storeVal.staffName}}
      <br/>
      <br/>
      {{storeVal.phoneNumber}}
      <br/>
      <br/>
      {{storeVal.storeName}}
      <br/>
      <br/>
      {{storeVal.provinceName}}
      <br/>
      <br/>
      {{storeVal.cityName}}
      <br/>
      <br/>
      {{storeVal.address}}
      <br/>
      <br/>
      {{storeVal.telephone}}
      <br/>
      <br/>
      {{storeVal.managementCycle}}
      <br/>
      <br/>
      {{storeVal.storeType=='1' ? '':''}}
      <br/>
      <br/>
      {{storeVal.operationMode == '1'?'':''}}
      <br>
      <br>
      <div>
        <span style="color: green;">{{status}}</span>
      </div>
    </div>

  </div>
</template>
<script>
  import { getProvinces,getCities ,userRegister,findAuditOfFailure,checkStorePhone} from '../../interface';

  export default{
    name: 'registerResult',
    data(){
      return{
        search:'',
        storeVal: {
          id: '',
          storeName: '',
          cityName:'',
          telephone: '',
          staffName: '',
          phoneNumber: '',
          operationMode: '',
          storeType: '',
          managementCycle: '',
          address: '',
          storeStatus: '',
          provinceId:'',
          cityId:'',
        },
        provincesData:[],
        citiesData:[],
        isShow: false,
        isShow2:false,
        status:'',
        successTag:''
      }
    },
    created(){
      this.getProvinces();
    },
    methods:{
      returnLogin(){
        this.$router.push({name: 'login'});
      },
      getProvinces(){
        this.$ajax({
          method: 'GET',
          url: getProvinces()
        }).then( (res) =>{
          this.provincesData = res.data;
        })
      },
      getCities(id){
        this.$ajax({
          method: 'GET',
          url: getCities()+id,
        }).then( (res) =>{
          this.citiesData = res.data;
        })
      },
      searc(){
        if(this.search ==''){
          this.$Message.error('');
          return;
        }
        this.$ajax({
          method:'GET',
          url:findAuditOfFailure()+'?phoneNumber='+this.search,
        }).then( (res) =>{
          this.storeVal = res.data;
          if(res.data!=null && res.data != ''){

            if(res.data.auditStatus =='1'){
              this.status ='';
              this.isShow = true;
              this.isShow2 =false;
            }else if(res.data.auditStatus == '2' ){
              this.status ='';
              this.isShow = false;
              this.isShow2 =true;
            }else if(res.data.auditStatus == '3' ){
              this.status ='';
              this.isShow = true;
              this.isShow2 =false;

            }
          }else{
            this.$Message.warning('');
          }

        }).catch( (error)=>{

        })

      },
      ok(){
        if(this.storeVal.staffName ==''|| this.storeVal.telephone ==''|| this.storeVal.storeName ==''|| this.storeVal.address ==''|| this.storeVal.phoneNumber ==''|| this.storeVal.managementCycle ==''|| this.storeVal.storeType ==''|| this.storeVal.operationMode ==''|| this.storeVal.provinceId=='' || this.storeVal.cityId==''){
          this.$Message.warning('');
          return;
        }
        this.$ajax({
          method: 'POST',
          url: userRegister(),
          data:this.storeVal
        }).then( (res) =>{
          this.$Message.success('');
          this.returnLogin();
        }).catch( (error) =>{
          this.$Message.error('');
        })
      }
    }
  }
</script>
<style scoped>
  .registerResult{
    width: 70%;
    margin: 0 auto;
    text-align: center;
  }
  .layout-logo-left{
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    font-size: 20px;
    padding: 22px 0;
    color: #66368C;
    /*background-color: #66368C;*/
    text-align: center;
    width: 500px;
    margin: 0 auto;
  }
  .Result{
    margin-top: 40px;
    /*border: 1px solid #000;*/
    text-align: left;
    /*padding-left: 30px;*/
  }
</style>



