<template>
  <div class="register">
    <div class="layout-logo-left"></div>
    <div>
      <Input v-model="storeVal.staffName" placeholder="" style="width: 315px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.phoneNumber" @on-keyup="storeVal.phoneNumber=check1(storeVal.phoneNumber)" placeholder="" style="width: 290px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.storeName" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="storeVal.provinceId" style="width:150px" :transfer=true  placeholder="" @on-change="getCities(storeVal.provinceId)">
        <Option :value="item.id" :key="item.id" v-for="item in provincesData">{{item.name}}</Option>
      </Select>
      <Select v-model="storeVal.cityId" style="width:150px" :transfer=true  placeholder="">
        <Option :value="item.id" :key="item.id" v-for="item in citiesData">{{item.name}}</Option>
      </Select>
      <br/>
      <br/>
      <Input v-model="storeVal.address" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.telephone" @on-keyup="storeVal.telephone=check1(storeVal.telephone)"  placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Input v-model="storeVal.managementCycle" @on-keyup="storeVal.managementCycle=check1(storeVal.managementCycle)" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="storeVal.storeType" style="width:300px" placeholder="">
        <Option value="1"></Option>
        <Option value="2"></Option>
      </Select>
      <br/>
      <br/>
      <Select v-model="storeVal.operationMode" style="width:300px" placeholder="">
        <Option value="1"></Option>
        <Option value="2"></Option>
      </Select>
      <br/>
      <br/>
    </div>
    <Button class="hy_btn" style="margin-left:20px;" size="large" @click="ok"></Button>
    <Button class="hy_btn" style="margin-left:20px;" size="large" @click="search"></Button>
    <Button class="hy_return" size="large" @click="returnLogin"></Button>

  </div>
</template>

<script>
  import { getProvinces,getCities ,userRegister,checkStorePhone} from '../../interface';
  export default{
    name: 'register',
    data(){
      return{
        storeVal: {
          id: '',
          storeName: '',
          cityName:'',
          telephone: '',
          staffName: '',
          phoneNumber: '',
          operationMode: '',
          storeType: '',
          managementCycle: '',
          address: '',
          storeStatus: '',
          provinceId:'',
          cityId:'',
        },
        provincesData:[],
        citiesData:[],
        successTag:''
      }
    },
    created(){
      this.getProvinces();
    },
    methods:{
      returnLogin(){
        this.$router.push({name: 'login'});
      },
      getProvinces(){
        this.$ajax({
          method: 'GET',
          url: getProvinces()
        }).then( (res) =>{
          this.provincesData = res.data;
        })
      },
      getCities(id){
        this.$ajax({
          method: 'GET',
          url: getCities()+id,
        }).then( (res) =>{
          this.citiesData = res.data;
        })
      },
      ok(){
        if(this.storeVal.staffName ==''|| this.storeVal.phoneNumber ==''|| this.storeVal.storeName ==''|| this.storeVal.address ==''|| this.storeVal.telephone ==''|| this.storeVal.managementCycle ==''|| this.storeVal.storeType ==''|| this.storeVal.operationMode ==''|| this.storeVal.provinceId=='' || this.storeVal.cityId==''){
          this.$Message.warning('');
          return;
        }
        this.$ajax({
          method:'GET',
          url:checkStorePhone()+'?phoneNumber='+this.storeVal.phoneNumber
        }).then( (res)=>{
          this.successTag = res.success;
        }).catch( (err)=>{
        })
        if(this.successTag == false){
          this.$Message.error('');
          return;
        }
        this.$ajax({
          method: 'POST',
          url: userRegister(),
          data:this.storeVal
        }).then( (res) =>{
          this.$Message.success('');
          this.returnLogin();
        }).catch( (error) =>{
          this.$Message.error('');
        })
      },
      check1(value){
        return value.replace(/[^\d]/g,'');
      },
      check2(value){
        return value.replace(/[^\d\.]/g,'');
      },
      search(){
        this.$router.push({name: 'registerResult'});
      }
    }
  }
</script>

<style scoped>
  .register{
    width: 70%;
    margin: 0 auto;
    text-align: center;
  }
  .layout-logo-left{
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    font-size: 20px;
    padding: 22px 0;
    color: #66368C;
    /*background-color: #66368C;*/
    text-align: center;
    width: 500px;
    margin: 0 auto;
  }
  .hy_return{
    background-color: #aaa;
    color: #fff;
    margin-left:20px;

  }
  .hy_return:hover {
    border-color: #aaa;
    color: #aaa;
    background-color: #fff;
  }
</style>



