<template>
  <div class="list">
    <h2 style="margin: .6rem 0;"></h2>
    <Button class="hy_btn" @click="Add"></Button>
    <br/>
    <br/>
    <Table :columns="columns" :data="data"></Table>

    <Modal v-model="addF" title="" :mask-closable="false" @on-ok="ok" class="mod">
      <div class='com'><Input v-model="addData.extensionName" style="width: 300px"></Input></div>
      <div class='com'><Input v-model="addData.bucklePrice" style="width: 276px" @on-keyup="addData.bucklePrice =check2(addData.bucklePrice)" ></Input></div>
      <div class="com"><Input v-model="addData.cashPrice" style="width: 276px" @on-keyup="addData.cashPrice=check2(addData.cashPrice)"></Input></div>
      <div class='com'><Input v-model="addData.extensionValidity" placeholder="" style="width: 288px" @on-keyup="addData.extensionValidity=check(addData.extensionValidity)"></Input></div>
      <div class='com'><Select v-model="addData.performance" style="width:252px" :transfer=true>
        <Option value="1"> </Option>
        <Option value="2"> </Option>
      </Select>
        </div>
      <div class='com'><Select v-model="addData.actualOperation" style="width:252px" :transfer=true>
        <Option value="1"> </Option>
        <Option value="2"> </Option>
      </Select>
       </div>
      <div class='com'><Select v-model="addData.manualFee" style="width:252px" :transfer=true>
        <Option value="1"> </Option>
        <Option value="2"> </Option>
      </Select>
        </div>

      <div class="project">
        <h3><Button class="hy_btn" size="small" @click="Addproject"></Button></h3>
        <div v-for="item in addData.project" class="projectone">
          <div class='com'>
            <Select v-model="item.projectId" style="width:150px" :transfer=true>
              <Option :value="items.id" :key="items.id" v-for="items in projectList">{{items.projectName}}</Option>
            </Select>
            &nbsp;&nbsp;<Input v-model="item.extensionNumber" style="width: 100px" @on-keyup="item.extensionNumber=check(item.extensionNumber)"></Input>
          </div>
        </div>
      </div>
      <div class='group'>
        <h3></h3>
        <div class='com'>  <Input v-model="addData.quantity" style="width: 30px"/> <Input v-model="addData.reward" style="width: 50px" @on-keyup="addData.reward=check2(addData.reward)"/> <Input v-model="addData.exceedReward" @on-keyup="addData.exceedReward=check(addData.exceedReward)" style="width: 50px"/> </div>
      </div>
    </Modal>
  </div>
</template>

<script>
import {findExtension,saveExtension,editExtension,deleteExtension,findAllProject} from '../../interface'
    export default {
      name: "tk_m",
      data() {
        return {
          columns: [
            { title: '', key: 'extensionName',},
            { title: '', key: 'bucklePrice',},
            { title: '', key: 'cashPrice',},
            { title: '()', key: 'extensionValidity',},
            { title: '', key: 'performance',
              render: (h, params) => {
                return h('div',params.row.performance==1? '':'')
              }

            },
            { title: '', key: 'actualOperation',
              render: (h, params) => {
                return h('div',params.row.actualOperation==1? '':'')
              }
            },
            { title: '', key: 'manualFee',
              render: (h, params) => {
                return h('div',params.row.manualFee==1? '':'')
              }
            },
            {
              title: '',
              key: 'action',
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.mannger(params.row)
                      }
                    }
                  }, ''),
                  h('Button', {
                    props: {
                      type: 'warning',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.del(params.row, params.index)
                      }
                    }
                  }, ''),
                ]);
              }
            }
          ],
          data: [],
          addF: false,
          addData:{
            actualOperation: "",
            extensionMoney: "",
            extensionName: '',
            extensionValidity: "",
            id: "",
            manualFee: "",
            performance: "",
            project: [
              {
                extensionNumber: '',
                extensionValidity:'',
                projectId: '',
                projectName: ""
              }
            ],
            quantity: '',
            reward: "",
            storeName: "",
            storeId: this.$route.params.id,
            bucklePrice:"",
            cashPrice:"",
            exceedReward:"",
          },
          type: '',
          projectList:[],
        }
      },
      methods: {
        getProject(){
          this.$ajax({
            method:'get',
            url: findAllProject()+'?id='+this.$route.params.id,
          }).then( (res) =>{
            this.projectList = res.data;
          }).catch( (error) =>{

          });
        },
        getData(){
          this.$ajax({
            method:'get',
            url: findExtension()+'?storeId='+this.$route.params.id,
          }).then( (res) =>{
            this.data = res.data;
          }).catch( (error) =>{

          })
        },
        ok() {
          var Surl;

          if(this.addData.extensionName == ''){
            this.$Message.warning('');
            return;
          }
        if(this.type == 1){
          Surl = saveExtension();
        }else{
          Surl = editExtension();
        }
        this.$ajax({
          method: 'post',
          url: Surl,
          data: this.addData,
        }).then( (res) =>{
          this.$Message.success('');
          this.getData();
        }).catch( (error) =>{
          this.$Message.error('');
        })
        },
        Add(){
          this.type = 1;
        this.addF = true;
        this.addData={
              actualOperation: "",
              extensionMoney: "",
              extensionName: '',
              extensionValidity: '',
              id: '',
              manualFee: "",
              performance: "",
              project: [
              {
                extensionNumber: '',
                extensionValidity:'',
                projectId: '',
                projectName: ""
              }
            ],
              quantity: '',
              reward: "",
              storeName: "",
              storeId: this.$route.params.id,
              bucklePrice:'',
              cashPrice:''
          };
        },
        Addproject(){
          this.addData.project.push({
            extensionNumber: '',
            extensionValidity:'',
            projectId: '',
            projectName: ""
          });

        },
        mannger(data, i) {
          this.type = 0;
          this.addF =true;
          this.addData = JSON.parse(JSON.stringify(data));
          this.addData.performance=this.addData.performance == 1 ? '1': '2';
          this.addData.actualOperation=this.addData.actualOperation== 1 ? '1': '2';
          this.addData.manualFee=this.addData.manualFee== 1 ? '1': '2';

        },
        del(data, i) {
          this.$ajax({
          method: 'get',
          url: deleteExtension()+'?id='+data.id,
        }).then( (res) =>{
          this.$Message.success('');
          this.getData();
        }).catch( (res) =>{
          this.$Message.error('');
        })
        },
        check(value){
          return value.replace(/[^\d]/g,'');
        },
        check2(value){
          return value.replace(/[^\d\.]/g,'');
        }
      },
      created(){
        this.getProject();
        this.getData();
      }
    }
</script>

<style scoped>
  .list{
    padding: 10px;
  }
  .mod{
    line-height: 60px;
  }
  .com{
    margin: 10px 0;
  }
  .group{
    padding: 10px;
  }

</style>



