<template>
  <div style="padding: 1rem">
    <h2 style="padding: .6rem;"></h2>
    <Button class="hy_btn" @click="save"></Button>
    <div class="content">
      <Row>
        <h3 class="title"></h3>
          <Col span="12">
            <h4 class="titleB"></h4>
             <Checkbox label="1" v-model="data.exclusivePointList"></Checkbox>
             <Checkbox label="2" v-model="data.continuousService"> <input type="text" class="inputext" placeholder="" v-model="data.serviceTimes"/> </Checkbox>
             <Checkbox label="3" v-model="data.customerIntroduction"></Checkbox>
             <Checkbox label="4" v-model="data.technician"></Checkbox>
          </Col>
          <Col span="12">
            <h4 class="titleB"></h4>
             <Checkbox label="5" v-model="data.customerComplaint"></Checkbox>
             <Checkbox label="6" v-model="data.establishContact"><input type="text" class="inputext" placeholder="" v-model="data.establishContactN"/> </Checkbox>
             <Checkbox label="7" v-model="data.noStore"> <input type="text" class="inputext" placeholder="" v-model="data.noStoreN"/> </Checkbox>
             <Checkbox label="8" v-model="data.cashPoor"> <input type="text" class="inputext" placeholder="" v-model="data.cashPoorN"/> </Checkbox>
          </Col>
      </Row>
    </div>
  <div class="content">
      <Row>
        <h3 class="title"></h3>
        <Col span="24">
          <span></span>
          <Select style="width:100px" v-model="RA">
            <Option value="1" :key="1"></Option>
            <Option value="2" :key="2"></Option>
            <Option value="3" :key="3"></Option>
            <Option value="4" :key="4"></Option>
          </Select>
          <span></span>
          <Select style="width:100px" v-model="RB">
            <Option value="1" :key="1"></Option>
            <Option value="2" :key="2"></Option>
            <Option value="3" :key="3"></Option>
            <Option value="4" :key="4"></Option>
          </Select>
          <span></span>
          <Select style="width:100px" v-model="RC">
            <Option value="1" :key="1"></Option>
            <Option value="2" :key="2"></Option>
            <Option value="3" :key="3"></Option>
            <Option value="4" :key="4"></Option>
          </Select>
          <span></span>
          <Select style="width:100px"  v-model="RD">
            <Option value="1" :key="1"></Option>
            <Option value="2" :key="2"></Option>
            <Option value="3" :key="3"></Option>
            <Option value="4" :key="4"></Option>
          </Select>
        </Col>
      </Row>
    </div>
    <div class="content">
      <Row>
        <h3 class="title">
           <Checkbox label="9" v-model="data.customerClassification"></Checkbox>
        </h3>
        <Col span="8">
          <h4 class="titleB"> <Checkbox label="12" v-model="data.classA"></Checkbox></h4>
          <div>A</div>
          <div>B</div>
          <div>C</div>
        </Col>
        <Col span="8">
          <h4 class="titleB"></h4>
           <Checkbox label="13" v-model="data.bigCustomer"> <input type="text" class="inputext" placeholder="" v-model="data.bigCustomerN"/> </Checkbox>
           <Checkbox label="14" v-model="data.ordinaryCustomer"> <input type="text" class="inputext" placeholder="" v-model="data.ordinaryCustomerN"/> </Checkbox>
        </Col>
        <Col span="8">
          <h4 class="titleB"></h4>
           <Checkbox label="15" v-model="data.regularCustomers">  <input type="text" class="inputext" placeholder="" v-model="data.regularCustomersN"/> </Checkbox>
           <Checkbox label="15" v-model="data.sleepCustomer">  <input type="text" class="inputext" placeholder="" v-model="data.sleepCustomerN"/> </Checkbox>
           <Checkbox label="15" v-model="data.frozenCustomers">  <input type="text" class="inputext" placeholder="" v-model="data.frozenCustomersN"/> </Checkbox>
        </Col>
      </Row>
    </div>
    <div class="content">
      <Row>
        <h3 class="title">
           <Checkbox label="16" v-model="data.customerAnalysis"></Checkbox>
        </h3>
        <Col span="4">
          <h4 class="titleB"></h4>
           <Checkbox label="17" v-model="data.bloodType"></Checkbox>
           <Checkbox label="18" v-model="data.constellation"></Checkbox>
        </Col>
        <Col span="4">
          <h4 class="titleB"></h4>
           <Checkbox label="19" v-model="data.incomeMode"></Checkbox>
           <Checkbox label="20" v-model="data.annualIncome"></Checkbox>
        </Col>
        <Col span="4">
          <h4 class="titleB"></h4>
           <Checkbox label="21" v-model="data.objective"></Checkbox>
        </Col>
        <Col span="4">
          <h4 class="titleB"></h4>
           <Checkbox label="22" v-model="data.face"></Checkbox>
           <Checkbox label="23" v-model="data.body"></Checkbox>
        </Col>
        <Col span="4">
          <h4 class="titleB"></h4>
           <Checkbox label="24" v-model="data.internalCause"></Checkbox>
           <Checkbox label="25" v-model="data.externalcause"></Checkbox>
        </Col>
      </Row>
    </div>

    <!--<m_index_info_gl/>-->

  </div>
</template>

<script type="text/ecmascript-6">

  import { findStoreById, editStoreCustomer } from '../../interface';
  import m_index_info_gl from '../store/m_index_info_gl';

  export default {
    name: 'c_index',
    components: {m_index_info_gl},
    data(){
      return {
        RA: '',
        RB: '',
        RC: '',
        RD: '',
        data: {
          xxxx: '',
          id: '',
          exclusivePointList: false,
          continuousService: false,
          serviceTimes: '',
          customerIntroduction: false,
          technician: false,
          customerComplaint: false,
          establishContact: false,
          noStore: false,
          noStoreN: '',
          cashPoor: false,
          cashPoorN: '',
          classA: false,
          classB: false,
          classC: false,
          bigCustomer: false,
          bigCustomerN: '',
          ordinaryCustomer: false,
          ordinaryCustomerN: '',
          customerClassification: false,
          sleepCustomer: false,
          sleepCustomerN: '',
          regularCustomers: false,
          regularCustomersN: '',
          frozenCustomers: false,
          frozenCustomersN: '',
          customerAnalysis: false,
          bloodType: false,
          constellation: false,
          incomeMode: false,
          annualIncome: false,
          objective: false,
          face: false,
          body: false,
          internalCause: false,
          externalcause: false,
          advisorDesignation: []
        }
      };
    },
    created() {
      this.findStore();
    },
    methods: {
      findStore() {
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url: findStoreById()+"?id="+this.$route.params.id,
        }).then((res) => {
         this.getSort(res.data.advisorDesignation);
          this.data = res.data;
        }).catch((error) => {
        });
      },
      save() {
        this.data.advisorDesignation = this.RA+','+this.RB+','+this.RC+','+this.RD,
        this.data.id = this.$route.params.id;
        this.$ajax({
          method: 'POST',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          data: this.data,
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url: editStoreCustomer(),
        }).then((res) => {
          this.$Message.success('');
          this.findStore();
        }).catch((error) => {
        });
      },
      getSort(data) {
        data.split(',').map( (i, index) => {
          if(index == 0) {
            this.RA = i;
          }
          if(index == 1) {
            this.RB = i;
          }
          if(index == 2) {
            this.RC = i;
          }
          if(index == 3) {
            this.RD = i;
          }
        });
      },
    }
  };
</script>

<style scoped>
  .askText{
    widht:100%;
    word-wrap: break-word;
  }
  .title{
    border-bottom: 1px solid #eee;
    margin: 0 0 10px 0;
    padding-bottom: 10px;
  }
  .titleB{
    padding-bottom: 10px;
  }
  .content{
    border: 1px solid #e3e3e3;
    padding: 1rem;
    margin: 10px 0;
  }
  .inputext{
    width: 30px;
    margin: 0 3px;
    border: 1px solid #ccc;
  }
</style>



