<template>
  <div>
    <h2 style="padding: .6rem;"></h2>
    <Row :gutter="24" class="option">
      <Col span="2">
      <Button class="hy_btn" @click="newEm">{{card}}</Button>
      </Col>
    </Row>
    <h3 style="text-align: center;padding: .6rem;">{{card}}</h3>
    <Table :columns="columns" :data="data"></Table>

    <Modal  v-model="storeFlag" :mask-closable="false" :title="store" @on-ok="ok">
      <Select v-model="pis.problemId" placeholder="" style="width:323px" :transfer=true>
      <Option v-for="item in prds" :value="item.id" :key="item.id">{{ item.problem }}</Option>
    </Select>
      <br><br>
      <Input v-model="pis.schemeName" placeholder="" style="width: 300px"/>
      <br/>
      <br/>
      <Select v-model="pis.basicProgrammeIds" placeholder="" :multiple=true style="width:275px" :transfer=true>
      <Option v-for="item in projectList" :value="item.id" :key="item.id">{{ item.projectName }}</Option>
    </Select>
      <br/>
      <br/>
      <Select v-model="pis.optimalSchemeIds" placeholder="" :multiple=true style="width:275px" :transfer=true>
      <Option v-for="item in projectList" :value="item.id" :key="item.id">{{ item.projectName }}</Option>
    </Select>

      <br/>
      <br/>


    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  import {saveSolution,editSolution,findSolutionList,deleteSolution,findproblemList,findAllProject,} from '../../interface';

  export default {
    name: 'p_program',
    created() {
      this.getProject();
      this.getProblem();
      this.getData();
    },
    data(){
      return {
        storeFlag: false,
        store: '',
        card: '',
        pis: {
          storeId: this.$route.params.id,
          basicProgramme: "",
          basicProgrammeIds: [],
          enable: true,
          handlingDetails: "",
          id: "",
          optimalScheme: "",
          optimalSchemeIds: [],
          problemId:'' ,
          problemName: "",
          schemeName: "",
          storeName: "",
          symptomType: ""

        },
        columns: [
          {
            title: '',
            key: 'problemName'

          },
          {
            title: '',
            key: 'schemeName'
          },
          {
            title: '',
            key: 'basicProgramme'
          },
          {
            title: '',
            key: 'optimalScheme'
          },
          {
            title: '',
            key: 'action',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.mannger(params.row)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.del(params.row, params.index)
                    }
                  }
                }, ''),
              ]);
            }
          }
        ],
        data: [],
        projectList:[],
        prds:[],
      };
    },
    methods: {
      newEm() {
        this.storeFlag = true;
        this.store = '';
        this.pis = {
          storeId: this.$route.params.id,
          basicProgramme: "",
          basicProgrammeIds: [],
          enable: true,
          handlingDetails: "",
          id: "",
          optimalScheme: "",
          optimalSchemeIds: [],
          problemId:'' ,
          problemName: "",
          schemeName: "",
          storeName: "",
          symptomType: ""

        };
      },
      getProject(){
        this.$ajax({
          method:'get',
          url: findAllProject()+'?id='+this.$route.params.id,
        }).then( (res) =>{
          this.projectList = res.data;
        }).catch( (error) =>{

        });
      },
      getProblem(){
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url: findproblemList()+'?id='+this.$route.params.id,
        }).then((res) => {
          this.prds = res.data;
        }).catch((error) => {
        });
      },
      getData(){
        this.$ajax({
          method: 'GET',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          url: findSolutionList()+'?id='+this.$route.params.id,
        }).then((res) => {
          this.data = res.data;
        }).catch((error) => {
        });

      },
      ok() {
        let URL = saveSolution();
        if( this.store == '') {
          URL = editSolution();
        };
        if(this.pis.schemeName == ''){
          this.$Message.warning('');
          return;
        }
        this.pis.basicProgrammeIds = this.pis.basicProgrammeIds.toString();
        this.pis.optimalSchemeIds = this.pis.optimalSchemeIds.toString();
        console.log(this.pis);
        this.$ajax({
          method: 'POST',
          dataType: 'JSON',
          contentType: 'application/json;charset=UTF-8',
          headers: {
            "authToken": sessionStorage.getItem('authToken')
          },
          data: this.pis,
          url: URL,
        }).then((res) => {
          this.$Message.success('');
          this.getData();
        }).catch((error) => {
          this.$Message.error('');
        });
      },
      mannger(data) {
        this.pis = JSON.parse(JSON.stringify(data));
        if (typeof data.basicProgramme == 'string') {
          this.pis.basicProgrammeIds = data.basicProgrammeIds.split(',').map( (it, i) => {return +it});
        }
        if (typeof data.optimalScheme == 'string') {
          this.pis.optimalSchemeIds = data.optimalSchemeIds.split(',').map( (it, i) => {return +it});
        }
        this.storeFlag = true;
        this.store = '';
      },
      del(data, i) {
        let mess = confirm('');
        if (mess) {
          this.$ajax({
            method: 'GET',
            url:deleteSolution()+"?id=" + data.id,
            dataType: 'JSON',
            contentType: 'application/json;charset=UTF-8',
            headers: {
              "authToken": sessionStorage.getItem('authToken')
            },
          }).then((res) => {
            this.data.splice(i, 1);
            this.$Message.success('');
          }).catch((error) => {
            this.$Message.error('');
          });
        }
      },
    }
  };
</script>

<style scoped>

</style>



