<template>
  <div>
    <Row>
      <Col span="2" style="margin-left:5px;">
      <h3></h3>
      <Checkbox v-model="data.preSale"></Checkbox>
      <Checkbox v-model="data.aftermarket"></Checkbox>
      </Col>
      <Col span="2">
      <h3></h3>
      <Checkbox v-model="data.beautyCash"></Checkbox>
      <Checkbox v-model="data.beautyPractice"></Checkbox>
      </Col>
      <Col span="2">
      <h3></h3>
      <Checkbox v-model="data.hairdressingCash"></Checkbox>
      <Checkbox v-model="data.hairdressingSnap"></Checkbox>
      </Col>
      <Col span="2">
      <h3></h3>
      <Checkbox v-model="data.yogaCash"></Checkbox>
      <Checkbox v-model="data.yogaSnap"></Checkbox>
      </Col>
      <Col span="2">
      <h3></h3>
      <Checkbox v-model="data.productCash"></Checkbox>
      <Checkbox v-model="data.productSnap"></Checkbox>
      </Col>
      <Col span="2">
      <h3></h3>
      <Checkbox v-model="data.projectCash"></Checkbox>
      <Checkbox v-model="data.projectSnap"></Checkbox>
      </Col>
      <Col span="5">
      <h3></h3>
      <Checkbox v-model="data.passenger"></Checkbox>
      <Checkbox v-model="data.storeNumber"></Checkbox>
      <Checkbox v-model="data.firstStoreNumber"></Checkbox>
      <Checkbox v-model="data.firstTransactionNumber"></Checkbox>
      <Checkbox v-model="data.twoTurnover"></Checkbox>
      </Col>
      <Col span="4">
      <h3></h3>
      <Checkbox v-model="data.totalActualExercise"></Checkbox>
      <Checkbox v-model="data.unitPrice"></Checkbox>
      <Checkbox v-model="data.arrears"></Checkbox>
      </Col>

      <Col span="2">
      <Button class="hy_btn btn" @click="saveData()"></Button>
      </Col>
    </Row>
  </div>
</template>

<script>
  import { findConsultantMonthlyReport ,saveOrUpateConsultantMonthlyReport} from '../../interface';
    export default {
      name: "t1",
      data () {
        return {
          data: {
            aftermarket: false,
            arrears: false,
            beauty: false,
            beautyCash: false,
            beautyPractice: false,
            dateTime: false,
            enable: false,
            hairdressing: false,
            hairdressingCash: false,
            hairdressingSnap: false,
            highEndProjects: false,
            id: 0,
            memberStorageValue: false,
            passenger: false,
            personalPerformance: false,
            preSale: false,
            product: false,
            productCash: false,
            productSnap: false,
            projectCash: false,
            projectSnap: false,
            storeId: 0,
            storeName: '',
            yoga: false,
            yogaCash: false,
            yogaSnap: false,
            unitPrice:false,
            totalActualExercise: false,
            twoTurnover:false,
            firstTransactionNumber: false,
            firstStoreNumber: false,
            storeNumber: false,
            passenger: false,
          },
          model1:''
        }
      },
      created() {
        this.getData();
      },
      methods: {
        check(val){
          if(val == null){
            val = false;
            return val;
          }else {
            return val;
          }
        },
        getData() {
          this.$ajax({
            method: 'get',
            dataType: 'JSON',
            contentType: 'application/json;charset=UTF-8',
            url: findConsultantMonthlyReport()+"?id="+this.$route.params.id,
          }).then((res) => {
            for( var item in res.data){
              res.data[item] = this.check(res.data[item]);
            }
            this.data= res.data;
          }).catch((error) => {
            this.$Message.error('');
          });
        },
        saveData(){
          this.$ajax({
            method: 'post',
            dataType: 'JSON',
            contentType: 'application/json;charset=UTF-8',
            url: saveOrUpateConsultantMonthlyReport(),
            data: this.data,
          }).then((res) => {
            this.$Message.success('');
          }).catch( (error) =>{
            this.$Message.error('');
          })

        }
      }
    }
</script>

<style scoped>
  h3{
    margin: 10px 0;
  }
  .btn{
    margin-top: 25px;

  }
  div{
    padding-bottom: 20px;
  }
</style>



