<template>
  <div>
    <Row :gutter="24" class="option">
      <Col span="2">
        <Button class="hy_btn" @click="newEm"></Button>
      </Col>
    </Row>

    <Table :columns="columns" :data="data"></Table>

    <Modal  v-model="storeFlag" :title="store" @on-ok="ok">
      <Input v-model="m_gard" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="customerLast" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="name" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="xmzk" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="xjq" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="fljs" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="xffl" placeholder="" style="width: 300px"></Input>
      <br/>
      <br/>
      <Input v-model="hyrzk" placeholder="" style="width: 300px"></Input>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'm_index_set',
    data() {
      return {
        storeFlag: false,
        m_gard: '',
        customerLast: '',
        name: '',
        xmzk: '',
        hyrzk: '',
        xjq: '',
        fljs: '',
        xffl: '',
        store: '',
        columns: [
          {
            title: '',
            key: 'm_gard'
          },
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'customerLast'
          },
          {
            title: '',
            key: 'xmzk'
          },
          {
            title: '',
            key: 'hyrzk'
          },
          {
            title: '',
            key: 'xjq'
          },
          {
            title: '',
            key: 'fljs',
          },
          {
            title: '',
            key: 'xffl',
          },
          {
            title: '',
            key: 'action',
            render: (h, params) => {
              return h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.mannger(params.row)
                  }
                }
              }, '');
            }
          }
        ],
        data: [
          {
            id: 12138,
            m_gard: '',
            customerLast: 20000,
            name: '',
            xmzk: 9,
            hyrzk: 8,
            xjq: 400,
            fljs: 8,
            xffl: 8,
          },
          {
            id: 12138,
            m_gard: '',
            customerLast: 17000,
            name: '',
            xmzk: 9.5,
            hyrzk: 9,
            xjq: 200,
            fljs: 9,
            xffl: 9,
          },
        ]
      };
    },
    methods: {
      newEm() {
        this.storeFlag = true;
        this.store = '';
        this.m_gard = '';
        this.customerLast = '';
        this.name = '';
        this.xmzk = '';
        this.hyrzk = '';
        this.xjq = '';
        this.fljs = '';
        this.xffl = '';
      },
      ok() {},
      mannger(data) {
        this.m_gard = data.m_gard;
        this.customerLast = data.customerLast;
        this.name = data.name;
        this.xmzk = data.xmzk;
        this.hyrzk = data.hyrzk;
        this.xjq = data.xjq;
        this.fljs = data.fljs;
        this.xffl = data.xffl;
        this.storeFlag = true;
        this.store = '';
      }
    }
  };
</script>

<style scoped>

</style>



