<template>
  <div>
    <Row :gutter="10">
      <Col span="2" style="padding-top: 40px;margin-left:5px;">
      <Checkbox v-model="data.passenger"></Checkbox>
      </Col>
      <Col span="3" >
      <h3></h3>
      <Checkbox v-model="data.storagePerformance"></Checkbox>
      <Checkbox v-model="data.cashCourse"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.facialPracticeAppoint"></Checkbox>
      <Checkbox v-model="data.facialPracticeNonSpecifiedt"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.physicalExerciseAppoint"></Checkbox>
      <Checkbox v-model="data.physicalExerciseNonSpecifiedt"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.projectCash"></Checkbox>
      <Checkbox v-model="data.projectSnap"></Checkbox>
      </Col>
      <Col span="3">
      <h3></h3>
      <Checkbox v-model="data.productCash"></Checkbox>
      <Checkbox v-model="data.productSnap"></Checkbox>
      </Col>
      <Col span="5">
      <h3></h3>
      <Checkbox v-model="data.cardPerformance"></Checkbox>
      <Checkbox v-model="data.giveHandToHand"></Checkbox>
      <Checkbox v-model="data.introducePerformance"></Checkbox>
      </Col>
      <Col span="1">
      <Button class="hy_btn btn" @click="saveData()"></Button>
      </Col>
    </Row>







  </div>
</template>

<script>
  import { findBeauticianMonthlyReport ,saveOrUpateBeauticianMonthlyReport} from '../../interface';

    export default {
      name: "t2",
      data () {
        return {
          data: {
            passenger : false,
            facialPracticeAppoint: false,
            facialPracticeNonSpecifiedt: false,
            physicalExerciseAppoint: false,
            physicalExerciseNonSpecifiedt: false,
            productCash: false,
            productSnap: false,
            projectCash: false,
            projectSnap: false,
            introducePerformance: false,
            giveHandToHand: false,
            storeId: '',
            cardPerformance: false,
            storagePerformance: false,
            cashCourse: false,

          },
          model1:'',
        }
      },
      created() {
        this.getData();
      },
      methods: {
        check(val){
          if(val == null){
            val = false;
            return val;
          }else {
            return val;
          }
        },
        getData() {
          this.$ajax({
            method: 'get',
            dataType: 'JSON',
            contentType: 'application/json;charset=UTF-8',
            url: findBeauticianMonthlyReport()+"?id="+this.$route.params.id,
          }).then((res) => {
            for( var item in res.data){
              res.data[item] = this.check(res.data[item]);
            }
            this.data= res.data;
          }).catch((error) => {
            this.$Message.error('');
          });
        },
        saveData(){
          this.$ajax({
            method: 'post',
            dataType: 'JSON',
            contentType: 'application/json;charset=UTF-8',
            url: saveOrUpateBeauticianMonthlyReport(),
            data: this.data,
          }).then((res) => {
            console.log(this.data);
            this.$Message.success('');
          }).catch( (error) =>{
            this.$Message.error('');
          })

        }
      }
    }
</script>

<style scoped>
  h3{
    margin: 10px 0;
  }
  .btn{
    margin-top: 25px;

  }
</style>



