<template>
  <div class="sa_index">
    <h2 style="margin: .6rem 0;"></h2>

    <h2> <Button class="hy_btn btn"  @click="save"></Button><Button class="hy_btn btn"  @click="more"></Button></h2>
    <br>
    <h3>
      <Checkbox v-model="s1" disabled></Checkbox>
    </h3>
    
    <Select v-model="data.typeOfBaseSalary" size="small" style="width:150px;margin-right: 10px;">
      <Option v-for="item in cityList2" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
    <Select v-model="data.monthlyCashType" size="small" style="width:150px;margin-right: 10px;" v-if="data.typeOfBaseSalary==1||data.typeOfBaseSalary ==2">
      <Option  value=""></Option>
      <Option  value=""></Option>
    </Select>
    <br>
    <br>

    <Row>
      <Col span="8">
      <h3>
        <Checkbox v-model="data.practicalExercises"></Checkbox>
      </h3>
      </Col>
      <Col span="8">
      <h3>
        <Checkbox v-model="data.performanceDrawinges"></Checkbox>
      </h3>
      </Col>
      <Col span="8">
      <h3>
        <Checkbox v-model="data.manualFee"></Checkbox>
      </h3>
      </Col>
    </Row>
    <br>

    <h3></h3>
    
    <Select v-model="data.typeOfBonus" size="small" style="width:150px;margin-right: 10px;">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
    <Select v-if="data.typeOfBonus==2" v-model="data.cashType" size="small" style="width:150px;margin-right: 10px;">
      <Option value="" ></Option>
      <Option value="" ></Option>
    </Select>
    <Checkbox v-model="data.teamBonuses"></Checkbox>
    <Checkbox v-model="data.activityBonuses"></Checkbox>
    <br>
    <br>

    <h3></h3>
    :<Select v-model="data.typeOfLeave" size="small" style="width:150px;margin-right: 10px;">
    <Option value="1" ></Option>
    <Option value="2" ></Option>
  </Select>
    <Checkbox v-model="data.complaintFines"></Checkbox>
    <Checkbox v-model="data.leaveEarlyFines"></Checkbox>
    <Checkbox v-model="data.absenteeismFine"></Checkbox>
    <Checkbox v-model="data.cashPerformancePpenalty"></Checkbox>
    <Checkbox v-model="data.passengerFines"></Checkbox>
    <Checkbox v-model="data.consumptionPenalty"></Checkbox>
    <Checkbox v-model="data.otherFines"></Checkbox>


  </div>
</template>


<script>
  import {findBonusesJsonByStore,editSalarySystem,findSalaryByStore} from '../../interface'
  import sa_index2 from './sa_index2'

  export default {
    name: "sa_index",
    data(){
      return{
        s1: true,
        data:{
          practicalExercises: false,
          performanceDrawinges: false,
          manualFee: false,
          teamBonuses: false,
          activityBonuses: false,
          complaintFines: false,
          leaveEarlyFines: false,
          leaveAfine: false,
          absenteeismFine: false,
          cashPerformancePpenalty: false,
          passengerFines: false,
          consumptionPenalty: false,
          otherFines: false,
          typeOfBaseSalary: '',
          typeOfBonus: '',
          storeId: this.$route.params.id,
          storeName: '',
          id: '',
          absenteeismFineAmount:'',
          accumulativePassengerFlow:'',
          accumulativePassengerFlowAward: '',
          baseSalary:'',
          complaintFinesAmount: '',
          oneDayPassengerFlowAward: '',
          singleDayPassengerFlow: '',
          monthlyCashType: '',
          cashType: '',
          typeOfLeave: '',
          leaveThePenaltyRules:''
        },
        data1:{},
        cityList:[
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: ''
          },
          {
            value: '3',
            label: ''
          }

        ],
        cityList2:[
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: ''
          },
          {
            value: '3',
            label: ''
          },
          {
            value: '4',
            label: ''
          },
          {
            value: '5',
            label: ''
          },
          {
            value: '6',
            label: ''
          }
        ],
        practicalExercises:'',
        data2:{},
        num: 0,
      }
    },
    methods:{
      getData(){
        this.$ajax({
          method: 'get',
          url:findBonusesJsonByStore()+'?id='+this.$route.params.id
        }).then( (res) =>{
          this.data1 = res.data;
        }).catch( (error) =>{
        });
        this.$ajax({
          method: 'get',
          url:findSalaryByStore()+'?id='+this.$route.params.id
        }).then( (res) =>{
          this.data = res.data;
          this.data.oneDayPassengerFlowAward = this.data1.oneDayPassengerFlowAward;
          this.data.singleDayPassengerFlow = this.data1.singleDayPassengerFlow;
          this.data.accumulativePassengerFlow = this.data1.accumulativePassengerFlow;
          this.data.accumulativePassengerFlowAward = this.data1.accumulativePassengerFlowAward;
          this.data.cashType = this.data1.cashType;
          this.data.storeId = this.$route.params.id;

        }).catch( (error) =>{
        });

      },
      save(){
        this.$ajax({
          method: 'post',
          url:editSalarySystem(),
          data: this.data,
        }).then( (res) =>{
          this.$Message.success('');
          this.getData();
          // window.location.reload();
        }).catch( (err) =>{
          this.$Message.error('');
        })
      },
      more(){
        this.$router.push({name: 'sa_index2'})
      }
    },
    created(){
      this.getData();
    },
    components: {sa_index2 },
  }
</script>

<style scoped>
  .btn{
    margin: 0px 10px 0 0;
  }
  .sa_index{
    padding-left: 10px;
    padding-bottom: 50px;
  }
  .btnspan{
    display: inline-block;
    width: 80px;
    height: 30px;
    line-height:30px;
    border-radius: 3px;
    text-align: center;
    background-color: #66368C;
    color: #fff;
  }
</style>



