<template>
  <div>
    <Table :columns="columns1" :data="data1"></Table>
    <Modal  v-model="storeFlag" title=""  :mask-closable="false"  @on-ok="ok">
      <div style="padding-left: 20px;">
        {{storeVal.staffName}}
        <br/>
        <br/>
        {{storeVal.phoneNumber}}
        <br/>
        <br/>
        {{storeVal.storeName}}
        <br/>
        <br/>
        {{storeVal.provinceName}}
        <br/>
        <br/>
        {{storeVal.cityName}}
        <br/>
        <br/>
        {{storeVal.address}}
        <br/>
        <br/>
        {{storeVal.telephone}}
        <br/>
        <br/>
        {{storeVal.managementCycle}}
        <br/>
        <br/>
        {{storeVal.storeType=='1' ? '':''}}
        <br/>
        <br/>
        {{storeVal.operationMode == '1'?'':''}}
        <br>
        <br>
        <Select v-model="auditStatus" style="width:300px" placeholder="" :transfer=true>
        <Option value="2"></Option>
        <Option value="3"></Option>
        </Select>
        <div v-if="auditStatus=='3'" style="margin-top: 10px;">
          <Input v-model="causeOfFailure" type="textarea" :autosize="true" placeholder="" style="width: 275px"/>
        </div>
      </div>
    </Modal>
  </div>
</template>
<script>
import { findStoreRegister,getProvinces,getCities,auditStoreCustomer } from '../../interface';
  export default{
    name: 's_check',
    data(){
      return{
        storeVal: {
          id: '',
          storeName: '',
          cityName:'',
          telephone: '',
          staffName: '',
          phoneNumber: '',
          operationMode: '',
          storeType: '',
          managementCycle: '',
          address: '',
          storeStatus: '',
          provinceId:'',
          cityId:'',
        },
        columns1: [
          {
            title: '',
            key: 'code'
          },
          {
            title: '',
            key: 'storeName'
          },
          {
            title: '',
            key: 'address'
          },
          {
            title: '',
            key: 'phoneNumber'
          },
          {
            title: '',
            key: 'managementCycle'
          },
          {
            title: '',
            key: 'auditStatus',
            render:(h,params)=>{
              return h('div',params.row.auditStatus == 3 ?'':'')
            }
          },
          {
            title: '',
            width: 200,
            key: 'action',
            render: (h, params) => {
              return h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.check(params.row)
                    }
                  }
                }, '');
            }
          }
        ],
        storeFlag:false,
        provincesData:[],
        citiesData:[],
        data1:[],
        isSystem: sessionStorage.getItem('isSystem'),
        storeId: sessionStorage.getItem('storeId'),
        auditStatus:'',
        causeOfFailure:''

      }
    },
    created(){
      this.getList();
    },
    methods:{
      getList(){
        this.$ajax({
          method:'GET',
          url:findStoreRegister(),
        }).then( (res)=>{
          this.data1 = res.data.results;
        }).catch((error)=>{

        })

      },
      getProvinces(){
        this.$ajax({
          method: 'GET',
          url: getProvinces()
        }).then( (res) =>{
          this.provincesData = res.data;
        })
      },
      getCities(id){
        this.$ajax({
          method: 'GET',
          url: getCities()+id,
        }).then( (res) =>{
          this.citiesData = res.data;
        })
      },
      check(data){
        this.storeFlag = true;
        this.storeVal = data;
        this.auditStatus =data.auditStatus;
        this.causeOfFailure =data.causeOfFailure;
      },
      check1(value){
        return value.replace(/[^\d]/g,'');
      },
      check2(value){
        return value.replace(/[^\d\.]/g,'');
      },
      ok(){
        if(this.auditStatus==''){
          this.$Message.error('');
          return;
        }
        this.$ajax({
          method:'POST',
          url: auditStoreCustomer(),
          data:{
            id: this.storeVal.id,
            auditStatus: this.auditStatus,
            causeOfFailure: this.causeOfFailure
          }
        }).then( (res)=>{
          this.$Message.success('');
          this.getList();
        })
      }
    }
  }

</script>
<style scoped>

</style>



