<template>
    <div>
      <Row :gutter="24" class="option">
        <Col span="2">
         <Button class="hy_btn" @click="newEm"></Button>
        </Col>
      </Row>

      <Table :columns="columns" :data="data"></Table>

      <Modal  v-model="storeFlag" :title="store" @on-ok="ok">
        <Input v-model="sjxx" placeholder="" style="width: 300px"></Input>
      </Modal>
    </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'm_index_info_gl',
    data() {
      return {
        storeFlag: false,
        store: '',
        sjxx: '',
        columns: [
          {
            title: '',
            key: 'list'
          },
          {
            title: '',
            key: 'item'
          },
          {
            title: '',
            key: 'action',
            render: (h, params) => {
              return h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.mannger(params.row)
                  }
                }
              }, '');
            }
          }
        ],
        data: [
          {
            list: 1,
            item: '',
          },
          {
            list: 2,
            item: '',
          },
        ],
      };
    },
    methods: {
      ok() {},
      mannger(data) {
        this.storeFlag = true;
        this.sjxx = data.item;
        this.store = '';
      },
      newEm(data) {
        this.storeFlag = true;
        this.sjxx = '';
        this.store = '';
      },
    }
  };
</script>

<style scoped>

</style>



