<template>
  <div class="login">
    <div class="layout-logo-left"></div>
    <div style="padding:50px 20px 0 20px;" >
      <span class="txt"></span><Input v-model="data.account" placeholder="" style="width: 70%;" />
      <br><br><br>
      <span class="txt"></span><Input v-model="data.password" type="password" placeholder=""  style="width: 70%;" />
    </div>
    <br>
    <div class="notice"><span style="float: left;"><a href="#/register"></a></span><span style="float: right;"><a href="#/forget"></a></span></div>
    <div style="margin: 0 auto;text-align: center;"><Button class="hy_btn" size="large" style="width:400px;height: 40px; " @click="checkLogin"></Button></div>
  </div>
</template>

<script>
  import {userLogin} from '../../interface'
  export default{
    name: 'login',
    data(){
      return{
        data:{
          account:'',
          password:''
        }
      }
    },
    created(){},
    methods:{
      checkLogin(){
        if(this.data.account ==''||this.data.password==''){
          this.$Message.warning('');
          return;
        }
        this.$ajax({
          method: 'POST',
          url: userLogin(),
          data: this.data
        }).then( (res) =>{
          if(res.data.system == null){
            this.$Message.error('!');
          }else{
            this.$Message.success('');
            sessionStorage.setItem('authToken',res.data.authToken);
            sessionStorage.setItem('isLogin','1');
            sessionStorage.setItem('isSystem',res.data.system);
            sessionStorage.setItem('storeId',res.data.storeId);
            sessionStorage.setItem('reData',JSON.stringify(res.data));
            this.$router.push({name: 'main'});
          }
        }).catch( (err) =>{
          this.$Message.error('');
        })

      },

    }
  }

</script>

<style scoped>
  .login{
    width:400px;
    /*height: 350px;*/
    margin: 100px auto;
    padding-bottom: 30px;
    /*border: 1px solid  #66368C;*/
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
  }
  .txt{
    display: inline-block;
    width: 25%;
    font-size: 16px;
    line-height: 30px;
  }
  .layout-logo-left{
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    font-size: 20px;
    padding: 22px 0;
    color: #fff;
    background-color: #66368C;
    text-align: center;
  }
  .notice{
    font-size: 13px;
    color: #999999;
    padding: 0 40px 0 20px;
    box-sizing: border-box;
    line-height: 30px;
    height: 30px;
    margin: 10px 0;
  }
  a:link {color: #999999}		/*  */
  a:visited {color: #999999}	/*  */
  a:hover {color: #999999}	/*  */
  a:active {color: #999999}
</style>



